import instance from './api';
import axios from 'axios';
export const signup=body=>{
   axios.post(`http://34.136.20.156/api/auth/signup`, body)
}
export const signout=body=>{
   instance.post(`http://34.136.20.156/api/auth/signout`)
}
export  const login =creds =>{
    return axios.post("http://34.136.20.156/api/auth/login",creds)
    
}
export const searchFlight=(body)=>{
   return axios.get(`http://34.136.20.156/api/flights/searchFlight?from=${body.from}&to=${body.to}&depDate=${body.depDate}&ticketNumber=${body.ticketNumber}`)
 }
export const getFlights=body=>{
   return instance.post(`http://34.136.20.156/api/flights`)
}
export const getAirports=(pageSize,currentPage)=>{
    
   return axios.get(`http://34.136.20.156/api/airports`)
}
export const getCities=()=>{

    return axios.get(`http://34.136.20.156/api/cities`)
 }
 export const updateUser=(id,body)=>{

    return instance.put(`http://34.136.20.156/api/Users/${id}`,(body))
 }
 export const getUser=(id)=>{

    return instance.get(`http://34.136.20.156/api/Users/${id}`)
 }
 
   