
import React, { useState, useEffect } from "react"
import { getAirports , searchFlight } from "../api/apiCalls"
import { useRef } from "react"
import { useHistory } from "react-router-dom"

export const FlightSearch = (props) => {
    const [text, setText] = useState('')
    const [textDep, setTextDep] = useState('')
    const [suggestions, setSuggestions] = useState([])
    const [suggestionsDep, setSuggestionsDep] = useState([])
    let [airports , setAirports]=useState([]);
    let today = new Date().toISOString().slice(0, 10)
    const [params, setParams] = useState({
        ticketNumber:1,
        flights: null
    })
    let history = useHistory();


    const [flightSearch, setFlightSearch] = useState({
        from: null,
        to: null,
        depDate: null,
        ticketNumber: null
    });
    const toRef = useRef();
    const fromRef = useRef();

 
    useEffect(() => {
        const loadAirports = async () => {
            const response = await getAirports();
            setAirports(response.data)
        } 
        loadAirports();
        
    }, [])
    const onSuggestionHandler = (text) => {
        setText(text);
        setSuggestions([])
    }
    const onChangeHandler = (text) => {
        let matches = []
        if (text.length > 1) {
            matches = airports.filter(airport => {
                const regex = new RegExp(`${text}`, "gi");
                return airport.name.match(regex);
            })
        }
        setSuggestions(matches)
        setText(text);
    }
    const onSuggestionHandlerDep = (textDep) => {
        setTextDep(textDep);
        setSuggestionsDep([])
    }
    const onChangeHandlerDep = (textDep) => {
        let matches = []
        if (text.length > 1) {
            matches = airports.filter(airport => {
                const regex = new RegExp(`${textDep}`, "gi");
                return airport.name.match(regex);
            })
        }
        setSuggestionsDep(matches)
        setTextDep(textDep);
        
    }
    const onchangeFlight = (event) => {
        const { name, value } = event.target;
        setFlightSearch((search) => ({ ...search, [name]: value }))
    }
    const onClickSearhFlight =async (event)=>{
        
        event.preventDefault();
        let body={
          depDate:flightSearch.depDate,
          ticketNumber:flightSearch.ticketNumber,
          from:fromRef.current.value,
          to:toRef.current.value
        }
         body.from=body.from.substring(body.from.length-1, body.from.length-4)
         body.to=body.to.substring(body.to.length-1, body.to.length-4)
         try {
            console.log(body)
            const flightResponse=await searchFlight(body);
            if(flightResponse.data){
                params.ticketNumber=body.ticketNumber;
                params.flights=flightResponse.data
                history.push({
                    pathname: "/flights",
                      state: params ,
                      search:`?from=${body.from}&to=${body.to}&depDate=${body.depDate}&ticketNumber=${body.ticketNumber}`
                  })
            }
         } catch (error) {
           
        }
        
    }
    return (
        <div>
            <div style={{ marginTop: "200px" }} className="wrapper bg-white ">
                <form action="#">
                    <div className="form-group border-bottom d-flex align-items-center justify-content-between flex-wrap">
                        <label className="option text-dark ms-auto me-auto my-sm-0 my-2">
                            <h1 >Search Flight</h1>
                        </label>
                    </div>
                    <div className="form-group d-sm-flex margin">
                        <div className=" align-items-center flex-fill me-sm-1 my-sm-0 my-4 border-bottom position-relative">
                            <input autoComplete="off" type="text" ref={fromRef} name="to" required placeholder="From" className="form-control" value={text} onChange={e => onChangeHandler(e.target.value)}
                            />
                            <div className="label" id="from" />
                            <span className="fas fa-dot-circle text-muted" />
                            {suggestions && suggestions.map((suggestion, i) =>
                                <div on onClick={() => onSuggestionHandler(suggestion.name)} className="suggestion align-items-center  me-sm-1 my-sm-0 my-4 border-bottom position-relative" key={i}>
                                    <label name='from'className="fas fa-dot-circle text-muted"> {suggestion.name} </label></div>
                            )}
                        </div>

                        <div className=" align-items-center flex-fill me-sm-1 my-sm-0 my-4 border-bottom position-relative">
                            <input  autoComplete="off" ref={toRef} type="text" required placeholder="To" className="form-control" value={textDep} onChange={e => onChangeHandlerDep(e.target.value)}
                            />
                            <div className="label" id="to" />
                            <span className="fas fa-dot-circle text-muted" />
                            {suggestionsDep && suggestionsDep.map((suggestionsDep, i) =>
                                <div on onClick={() => onSuggestionHandlerDep(suggestionsDep.name)} className="suggestion align-items-center  me-sm-1 my-sm-0 my-4 border-bottom position-relative" key={i}>
                                    <label name='to' onChange={onchangeFlight} className="fas fa-dot-circle text-muted"> {suggestionsDep.name} </label></div>
                            )}
                        </div>
                    </div>
                    <div className="form-group d-sm-flex margin">
                        <div className="d-flex align-items-center flex-fill me-sm1 my-sm-0 border-bottom position-relative">
                            <input type="date" name="depDate"   min={"2020-10-10"} max="2024-12-31" onChange= {onchangeFlight} required placeholder="ƒ∂ƒ Date" className="form-control" />
                            <div className="label" name="depart" />
                        </div>
                        <div className="d-flex align-items-center flex-fill ms-sm-1 my-sm-0 my-4 border-bottom position-relative">
                            <input type="date"  min={today} max="2024-12-31" onChange= {onchangeFlight} required placeholder="Return Date" className="form-control" />
                            <div className="label" name="return" />
                        </div>
                    </div>
                    <div className="form-group border-bottom d-flex align-items-center position-relative">
                        <input type="number" min="1" max="5" name="ticketNumber" onChange= {onchangeFlight} required placeholder="Traveller(s)" className="form-control" />
                        <div className="label" name="passenger" />
                        <span className="fas fa-users text-muted" />
                    </div>
                    <div className="form-group my-3">
                        <div name='searchFlight' onClick={onClickSearhFlight} className="btn btn-primary rounded-0 d-flex justify-content-center text-center p-3">Search Flights
                        </div>
                    </div>
                </form>
            </div>

        </div>

    )

}