import React from 'react';
import LoginPage from '../Pages/LoginPage';
import HomePage from '../Pages/HomePage';
import { HashRouter as Router, Route, Redirect, Switch } from "react-router-dom"
import UserSignupPage from '../Pages/UserSignupPage';
import TopBar from '../components/TopBar';
import UserPage from "../Pages/UserPage";
import { connect } from 'react-redux';
import UserTicketsPage from '../Pages/UserTicketsPage';
import { FlightList } from '../components/FlightList';
import {FlightSearch} from'../components/FlightSearch'
import Payment from '../Pages/Payment';
import CheckoutForm from '../Pages/CheckoutForm';
import TicketControl from '../Pages/TicketControl';
import CheckoutPage from '../Pages/CheckoutPage';
class App extends React.Component {
  render() {
    const {isLoggedIn} =this.props;
    return (
      <div>
        <Router>
          <TopBar></TopBar>
          <Switch>
            <Route exact path="/" component={HomePage}></Route>
            {!isLoggedIn&& <Route path="/login" component={LoginPage}/> }
            <Route path="/signup" component={UserSignupPage}></Route>
            <Route path="/mytickets" component={UserTicketsPage} ></Route>
            <Route path="/user/:userFirstName" component={UserPage}></Route>
            <Route path="/payment" component={Payment}></Route>
            <Route path="/checkout" component={CheckoutForm}></Route>
            <Route path="/flights"  component={FlightList} ></Route>
            <Route path="/search"  component={FlightSearch} ></Route>
            <Route path="/ticketControl"  component={TicketControl} ></Route>
            <Route path="/checkoutpage"  component={CheckoutPage} ></Route>
            <Redirect to="/"></Redirect>
          </Switch>
        </Router>
      </div>
    );
  }
}

const mapStateToProps = (store) => {
    return {
        isLoggedIn:store.isLoggedIn,
        userFirstName : store.userFirstName
    }

}
export default connect(mapStateToProps)(App)

 