import React from 'react'
import StripeApp from "../stripe/StripeApp";
import { connect } from 'react-redux';
import { useState } from 'react';
import { useLocation } from 'react-router-dom';
const CheckoutPage = (props) => {
    const location = useLocation();
    const flightId = location.state.flightId;
    const ticketNumber = Number.parseInt(location.state.ticketNumber, 10);
    const travellers = location.state.travellers;
     const [createPayment, setCreatePayment] = useState({
        flightId :flightId,
        ticketNumber:ticketNumber,
        travellers:travellers,
        userId:props.id
    })
  return (
    <div >
         <div className="container">
             <div className="sticky-md-top">
                <h2 style={{marginTop:"150px"}}>Payment</h2>
                <StripeApp   props={createPayment} />
            </div>
        </div>
    </div>
  )
}

const mapStateToProps = (store) => {
    return {
        isLoggedIn: store.isLoggedIn,
        id: store.id,
    }

}
export default connect(mapStateToProps)(CheckoutPage)