import React, { useEffect, useState } from "react";

import {
  PaymentElement,
  useStripe,
  useElements
} from "@stripe/react-stripe-js";

export default function CheckoutForm(props) {
  const stripe = useStripe();
  const elements = useElements();

  const [paymentInfo, setPaymentInfo] = useState({});
  const [message, setMessage] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const onChange = event => {
    event.preventDefault();
    const { name, value } = event.target;
    setPaymentInfo((previousForm) => ({ ...previousForm, [name]: value }))
    
  }
  useEffect(() => {
    if (!stripe) {
      return;
    }

    const clientSecret = new URLSearchParams(window.location.search).get(
      "payment_intent_client_secret"
    );

    if (!clientSecret) {
      return;
    }

    stripe.retrievePaymentIntent(clientSecret).then(({ paymentIntent }) => {
      switch (paymentIntent.status) {
        case "succeeded":
          setMessage("Payment succeeded!");
          break;
        case "processing":
          setMessage("Your payment is processing.");
          break;
        case "requires_payment_method":
          setMessage("Your payment was not successful, please try again.");
          break;
        default:
          setMessage("Something went wrong.");
          break;
      }
    });
  }, [stripe]);

  const handleSubmit = async (e) => {
    e.preventDefault();
     
    if (!stripe || !elements) {
      return;
    }

    setIsLoading(true);
    const { city, line1, line2, state, email, phone, name } = paymentInfo

    const { error } = await stripe.confirmPayment({
      elements,
      confirmParams: {
         return_url: "http://localhost:8080/success",
        receipt_email: email,
        payment_method_data: {
          billing_details: {
            address: {
              city: city,
              line1: line1,
              line2: line2,
              state: state
            },
            email: email,
            name: name,
            phone: phone
          }
        }
      },

    });

    if (error.type === "card_error" || error.type === "validation_error") {
      setMessage(error.message);
    } else {
      setMessage("An unexpected error occurred.");
    }

    setIsLoading(false);
  };

  const paymentElementOptions = {
    layout: "tabs"
  }
  return (
    
    <div   style={{ fontSize:"13px",backgroundColor:"#FAFAFA"}} className ="containerPayment mt-5 px-5">
      <form className="needs-validation" id="payment-form" novalidate onSubmit={handleSubmit}>
      <h6 className="text-uppercase">Payment details</h6>
      <div className="row">
        <div className="col-md-6">
          <div className="card p-3">            
            <div className="mt-4 mb-4">
              <h6 className="text-uppercase">Billing Address</h6>
              <div className="row mt-3">
                <div className="col-md-6">
                  <label>Street Address</label>
                  <div className="inputbox mt-3 mr-2 "> <input id="form" type="text" onChange={onChange} name="line1" className="form-control  "  required />  </div>
                 
                </div>
                <div className="col-md-6">
                  <label>City</label>
                  <div className="inputbox mt-3 mr-2"> <input  id="form"  type="text" name="city" onChange={onChange} className="form-control "  /></div>
                 
                </div>
              </div>
              <div className="row mt-2">
                <div className="col-md-6">
                  <label>State/Province</label>
                  <div className="inputbox mt-3 mr-2"><input  id="form"  type="text" name="state" onChange={onChange} className="form-control "  /></div>
                   
                </div>
                <div className="col-md-6">
                  <label>Zip code</label>
                  <div className="inputbox mt-3 mr-2"> <input id="form"  type="text" name="name" onChange={onChange}  className="form-control "  /> </div>
                  
                </div>
                <div className="col-md-6">
                  <label>Phone Number</label>
                  <div className="inputbox mt-3 mr-2"> <input  id="form" onChange={onChange}  type="tel"  placeholder="ex: 642 156 1226" className="form-control "  name="phone"   /> </div>
                
                </div>
                <div className="col-md-6">
                  <label>Email</label>
                  <div className="inputbox mt-3 mr-2"> <input  id="form"  type="email" name="email" onChange={onChange}  className="form-control "  /> </div>
                 
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6">

         

            <PaymentElement id="payment-element" options={paymentElementOptions} />  
            <label>Name on card</label>
            <div className="inputbox mt-3"><input type="text"  id="form"  name="name" className="form-control"  />  </div>
            <button  className="form-group btn btn-primary" disabled={isLoading || !stripe || !elements} id="submit">
              <span id="button-text">
                {isLoading ? <div className="spinner" id="spinner"></div> : "Pay now"}
              </span>
            </button>
            {message && <div id="payment-message">{message}</div>}
         
        </div>
      </div>
      </form>
    </div>

    


  );
}