import React, { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import CheckoutForm from "../Pages/CheckoutForm";
import { useLocation } from "react-router-dom";

const stripePromise = loadStripe("pk_test_51M5fyKBOWD1rcLcdbY4JYhEzMjHtDi8TvRjp1IbgfkqwI9QINXJskRGK1xH13Ik9vJg1Y6IKCFaqbwjqQ85GqGGZ00iHNNVNsU");


const StripeApp =(props)=> {
  const location=useLocation();
  const flightId=location.state.flightId;
  const ticketNumber=location.state.ticketNumber;
  const travellers=location.state.travellers;
  const createPayment={
      flightId:flightId,
      ticketNumber:ticketNumber,
      travellers:travellers
  }
   const [clientSecret, setClientSecret] = useState("");
  useEffect(() => {
    fetch("/create-payment-intent", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(createPayment),
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, []);

  const appearance = {
    theme: 'stripe',
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
    <div className="Stripe">
      {clientSecret && (
        <Elements options={options} stripe={stripePromise}>
          <CheckoutForm />
        </Elements>
      )}
    </div>
  );
}
export default StripeApp;