import React, { useEffect, useState } from 'react'
import { connect,useDispatch } from 'react-redux';
import { updateUser ,getUser } from '../api/apiCalls';
import ProfileImageWithDefault from '../components/ProfileImageWithDefault';
import { updateSuccess } from '../redux/authActions';
const UserPage = (props) => {
  const [updatedValue,setUpdatedValue]=useState({
    userEmail:undefined,
    userPhoneNumber:undefined,
    userGender : undefined
  })
  const dispatch=useDispatch();
  const [userInformation,setUserInformation] =useState([]);
  const [newImage ,setNewImage]=useState();
  const [validationErrors,setValidationErros]=useState([]);
  const userid=props.id
  useEffect(()=>{
    const loadUser=async()=>{
        const response=await getUser(userid);
        setUserInformation(response.data)
    }
    loadUser(); 
},[])

  const clearAllValue=()=>{
      Array.from(document.querySelectorAll("input")).forEach(
        input => (input.value = "")
      );
      Array.from(document.querySelectorAll("radio")).forEach(
        radio => (radio.value="") 
      );
      setUpdatedValue(
        updatedValue.userEmail=undefined,
        updatedValue.userPhoneNumber=undefined,
        updatedValue.userGender=undefined
      )
      setNewImage(undefined)
  }

  const onChangeUpdate=(event)=>{
    const { name, value } = event.target;
    const errorsCopy ={...validationErrors};
    errorsCopy[name]=undefined;
   setValidationErros(errorsCopy)
      setUpdatedValue((previousForm)=>({...previousForm,[name]:value}))
  }
  const onChangeFile=(event)=>{
    if(event.target.files.length<1){
      return
    }
    const file=event.target.files[0]
    const fileReader=new FileReader();
    fileReader.onloadend=()=>{
      setNewImage(fileReader.result)
    }
    fileReader.readAsDataURL(file)
  }
 
  const onClickUpdate= async()=>{
   
    let userImage;

    if(newImage){
      userImage= newImage.split(',')[1]
    }
    
    const body={
      id:userInformation.id,
      userEmail: updatedValue.userEmail,
      userPhoneNumber: updatedValue.userPhoneNumber,
      userGender: updatedValue.userGender,
      userFirstName: userInformation.userFirstName ,
      userLastName: userInformation.userLastName,
      userImage
    }
    try{
      const response =await updateUser(userInformation.id,body)
      setUserInformation(response.data)
      dispatch(updateSuccess(response.data))
    }catch(error){
      setValidationErros(error.response.data.validationErrors)
    }
  }
  const userName = userInformation.userFirstName + " " + userInformation.userLastName ;
  const {userEmail :userEmailError , userPhoneNumber:userPhoneNumberError}=validationErrors
  return (
    <div style={{ marginTop: "175px" }}>
      <section style={{ backgroundColor: '' }}>
        <div className="container userPages">
          <div className="row gutters">
            <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
              <div className="card h-100">
                <div className="card-body">
                  <div className="account-settings">
                    <div className="user-profile">
                      <div className="user-avatar">
                        <ProfileImageWithDefault
                          className="rounded-circle shadow"
                          width="200" heught="200"
                          alt={`${userInformation.userFirstName} profile`}
                          image={userInformation.userImage}
                          tempimage={newImage}
                        ></ProfileImageWithDefault>
                      </div>
                      <h5 className="user-name">{userName}</h5>
                      <h6 className="user-email">{userInformation.userEmail}</h6>
                      <h6 className="user-phone-number">{userInformation.userPhoneNumber}</h6>
                      <h6 className="user-gender">{userInformation.userGender}</h6>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-xl-9 col-lg-9 col-md-12 col-sm-12 col-12">
              <div className="card h-100">
                <div className="card-body">
                  <div className="row gutters">
                    <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                      <h6 className="mb-2 text-primary">Personal Details</h6>
                    </div>

                    <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                      <div className="form-group">
                        <label htmlFor="eMail">Email</label>
                        <input type="email" className="form-control" name="userEmail"  onChange={onChangeUpdate}  />
                        {userEmailError&&<div className="text-danger"><small>{userEmailError}</small></div>}
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                      <div className="form-group">
                        <label htmlFor="phone">Phone</label>
                        <input type="text" className="form-control" name="userPhoneNumber" onChange={onChangeUpdate} />
                        {userPhoneNumberError&&<div className="text-danger"><small>{userPhoneNumberError}</small></div>}
                      </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                      <label htmlFor="phone">Phone</label>
                      <div className="form-check">
                        <label className="form-check-label">
                          <input type="radio" className="form-check-input" name="userGender" onChange={onChangeUpdate}  value="FEMALE"/> Female
                        </label>
                      </div>
                      <div className="form-check">
                        <label className="form-check-label">
                          <input type="radio" className="form-check-input" name="userGender" onChange={onChangeUpdate}  value="MALE"/> Male
                        </label>
                      </div>
                      <div className="form-check">
                        <label className="form-check-label">
                          <input type="radio" className="form-check-input" name="userGender" onChange={onChangeUpdate}  value="OTHERS"/> Others
                        </label>
                      </div>
                    </div>
                  </div>
                  <div>
                  <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                      <input className='form-control' onChange={onChangeFile} type="file"></input>
                  </div>
                  <div className="mt-5 col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12">
                      <div className="form-group">
                         <button  className='btn btn-primary me-5 ' onClick={onClickUpdate}>Update</button>
                         <button className='btn btn-danger' onClick={clearAllValue}>Cancel</button>
                      </div>
                    </div>   
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>

  )

}
const mapStateToProps = (store) => {
  return {
    isLoggedIn: store.isLoggedIn,
      id: store.id
  }

}
// const mapDispatchProps = (dispatch) => {

//   return {
//       onLogoutSuccess: () => {
//           dispatch(logoutSuccess());
//       }
//   }
// }
export default connect(mapStateToProps)(UserPage)

