import { createStore, applyMiddleware } from "redux";
import AuthReducer from "./AuthReducer";
import SecureLS from "secure-ls";
import thunk from "redux-thunk";

const securls = new SecureLS();

const getStateFromStorage =()=>{
    const renasAuth=securls.get('renas-auth')

    let stateInLocalStorage ={
        id:undefined,
        isLoggedIn: false, 
        userFirstName: undefined,
        userLastName : undefined,
        userEmail :undefined ,
        userPassword :undefined,
        userGender: undefined ,
        userImage:undefined
        }
    if(renasAuth){
        return renasAuth
    }
    return stateInLocalStorage
}
const updateStateStorage= newState =>{
    securls.set('renas-auth',newState);

}
 
export const  configureStore =()=>{
    
  
    const store =createStore(AuthReducer,getStateFromStorage(),applyMiddleware(thunk));

    store.subscribe(()=>{
        updateStateStorage(store.getState());
    })
    return store;

}
 
