import React, { useState } from "react";
import Input from "../components/Input";
import axios from "axios";
const UserSignupPage =(props)=>{
   const [form,setForm]=useState({
        userFirstName: null,
        userLastName: null,
        userPhoneNumber: null,
        userEmail: null,
        userPassword: null,
        userPasswordRepeat: null,
        pendingApiCall: false,
        userGender:null

    })
    const [errors,setErrors]=useState({});
    const [success,setSuccess]=useState(false);
   
    const onChange = event => {
        event.preventDefault();
        setSuccess(false);
        const { name, value } = event.target;
        const errorsCopy ={...errors};
        errorsCopy[name]=undefined;
       setErrors(errorsCopy)
       setForm((previousForm)=>({...previousForm,[name]:value}))
    }
   
    const onClickSignup = async event => {
        event.preventDefault();
        const { userFirstName, userLastName, userIdentityNumber, userEmail, userPhoneNumber, userPassword,userGender } = form
        const body = {
            userFirstName: userFirstName,
            userLastName: userLastName,
            userIdentityNumber: userIdentityNumber,
            userEmail: userEmail,
            userPhoneNumber: userPhoneNumber,
            userPassword: userPassword ,
            userGender: userGender
        };
        try{
                       
            const response =await  axios.post(`http://34.136.20.156/api/auth/signup`, body)

            if(response.status==201){
                setSuccess(true)
            }
            
        }catch(error){
            if(error.response.data.validationErrors){
              setErrors(error.response.data.validationErrors)
            }
        }
        
 
    }
   
    const {userFirstName :userFirstNameError,userLastName:userLastNameError,userIdentityNumber:userIdentityNumberError,userEmail:userEmailError,userPhoneNumber:userPhoneNumberError,userPassword:userPasswordError} =errors;
    let userPasswordRepeatError ;
    if(form.userPassword!==form.userPasswordRepeat){
        userPasswordRepeatError="Password Mismatch";
    }
    return (
        
        <div  style={{marginTop:"200px"}} className="containers ">
            <form className="form-group">
                <h1 className="text-center">Sign Up</h1>
                <Input name="userFirstName" label="First Name" error={userFirstNameError} onChange={onChange}></Input>
                <Input name="userLastName" label="Last Name" error={userLastNameError} onChange={onChange}></Input>
                <Input name="userEmail" label="Email" error={userEmailError} onChange={onChange}></Input>
                <Input name="userPhoneNumber" label="Phone" error={userPhoneNumberError} onChange={onChange}></Input>
                <Input name="userPassword" type="password" label="Password" error={userPasswordError} onChange={onChange}></Input>
                <Input name="userPasswordRepeat" type="password" label="Password Repeat" error={userPasswordRepeatError} onChange={onChange}></Input>
               <div className="form-group input">
                    <label>Gender</label>
                    <div className="form-check row ">
                        <label className="form-check-label col-4">
                            <input type="radio" className="form-check-input" name="userGender"  value="FEMALE"  onChange={onChange} /> Female
                        </label>
                        <label className="form-check-label col-4">
                            <input type="radio" className="form-check-input" name="userGender" value="MALE"    onChange={onChange}/> Male
                        </label>
                
                        <label className="form-check-label col-4">
                            <input type="radio" className="form-check-input" name="userGender" value="OTHERS"  onChange={onChange}/> Others
                        </label>
                    </div>
                </div>

                <div className="text-center">
                    <button disabled={userPasswordRepeatError!==undefined} className="btn btn-primary mt-3" onClick={onClickSignup}>Sign Up</button>
                    {(success) && <div className="alert alert-success" role="alert">User Created</div>}    
                </div>
            </form>
        </div>

    );
}

export default UserSignupPage;