import React from 'react'
import { useState } from 'react'
const TicketControl = (props) => {
  let today = new Date().toISOString().slice(0, 10)
  const [form, setForm] = useState({
    userFirstName: null,
    userLastName: null,
    userBirthDate:null,
    userGender:null
  })
  
  const onChange = event => {
    event.preventDefault();
    const { name, value } = event.target;
      setForm((previousForm) => ({ ...previousForm, [name]: value }))
      props.list[props.number]= form  
    }

  

  return (
    <div style={{backgroundColor:"white", fontSize:"13px"}} className="wrapper container border ">
      <form  action="#">
        <div className="form-group border-bottom d-flex align-items-center justify-content-between  flex-wrap">
          <label className="option text-dark  my-sm-0 my-2">
            <h4 >Traveler {props.number+1}</h4>
          </label>
        </div>
        <div className="form-group d-sm-flex margin">
          <label>First Name</label>
          <div className=" align-items-center flex-fill me-sm-1 my-sm-0 my-4  position-relative">
            <input type="text" name="firstname"  required className="form-control" onChange={onChange} />
          </div>
          <label>Last Name</label>
          <div className=" align-items-center flex-fill me-sm-1 my-sm-0 my-4 position-relative">
            <input type="text" name="lastname" required className="form-control" onChange={onChange} />
          </div>
        </div>
        
        <div className="form-group d-sm-flex margin">
        <label>Gender</label>
          <div style={{marginLeft:"5%"}} className="  align-items-center  my-sm-0 my-4 position-relative col-4">

             <input type="radio" className="form-check-input" required name="userGender" value="FEMALE" onChange={onChange} /> Female
         
            <input  style={{marginLeft:"5%"}}type="radio"  className="form-check-input" name="userGender" value="MALE" onChange={onChange} /> Male
          </div>
          <label>Birth Date</label>
          <div className="d-flex align-items-center flex-fill me-sm1 my-sm-0 border-bottom position-relative">
          
            <input type="date" name="userBirthDate"  max="2024-12-31" onChange={onChange} required placeholder="ƒ∂ƒ Birth Date" className="form-control" />
            <div className="label" name="birtdate" />
          </div>
        </div>
         
      </form>

    </div>




  );
}

export default TicketControl