import React, { useEffect, useState } from "react";
import Input from "../components/Input";
import { connect } from "react-redux";
import { loginHandler } from "../redux/authActions";

const LoginPage = (props) => {
  const [userEmail, setUserEmail] = useState();
  const [userPassword, setUserPassword] = useState();
  const [errorMessage, setErrorMessage] = useState();
  useEffect(() => {
    setErrorMessage(undefined);
  }, [userEmail, userPassword]);

  const onClickLogin = async (event) => {
    event.preventDefault();
    const { history, dispatch } = props;
    const { push } = history;

    const creds = {
      userEmail,
      userPassword,
    };
    setErrorMessage(undefined);
    try {
      await dispatch(loginHandler(creds));
      push("/");
    } catch (apiError) {
      setErrorMessage(true);
    }
  };
  const buttonEnabled = userEmail && userPassword;

  return (
    <div   style={{marginTop:"200px"}} className="containers">
      < h1 className="text-center">Login</h1>
      <form>
        <div className="text-ce">
        <Input label="Email" name="userEmail" onChange={(event) => { setUserEmail(event.target.value); }}></Input>
        <Input label="Password" name="userPassword" type="password"onChange={(event) => {setUserPassword(event.target.value);}}></Input>
        {errorMessage && (<div className="alert alert-danger" role="alert">Wrong Email Or Password</div>)}
        </div>
        <div className="text-center">
            <button  disabled={!buttonEnabled}  className="btn btn-primary   mt-2 "onClick={onClickLogin}>Login</button>
        </div>
      </form>
    </div>
  );
};
export default connect()(LoginPage);
