import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { logoutSuccess } from '../redux/authActions'
import { useScrollPosition } from '../container/useScrollPosition';
import ProfileImageWithDefault from './ProfileImageWithDefault';

const TopBar = props => {


    function classNames(...classes) {
        return classes.filter(Boolean).join(' ')
    }
    const scrollPosition = useScrollPosition();
    const { isLoggedIn, onLogoutSuccess, username ,userImage } = props;
    let links = (
        <ul className='navbar-nav  ms-auto '>
            <button className={classNames(scrollPosition > 0 ? ' text-white border border-dark ' : ' text-dark', ' btn  shadow')}><Link className='nav-link ' to="/login"> Login</Link></button>
            <button className='btn btn-primary'><Link className='nav-link ' to="/signup">Signup </Link></button>
        </ul>
    )
    if (isLoggedIn) {
        links = (
            <ul className=' navbar-nav ms-auto '>
                <div>
                    
                    <li className={classNames(scrollPosition > 0 ? 'border border-dark ' : '', ' nav-link btn text-dark   dropdown-toggle ')} id="dropdownMenuLink" data-toggle="dropdown" >{username}
                    <ProfileImageWithDefault image={userImage} widht="30" height='28' className='ms-1 rounded-circle'></ProfileImageWithDefault>
                    </li>
                    <div className="dropdown show">
                        <div className="dropdown-menu  " aria-labelledby="dropdownMenuLink">
                            <Link className="nav-link underline text-dark dropdown-item"  to="/signup" >My Tickets</Link>
                            <Link className="nav-link underline text-dark  dropdown-item"   to={`/user/${username}`} >My Profile</Link>
                            <Link className="nav-link underline text-dark dropdown-item"  to="/signup" >Action</Link >
                        </div>
                    </div>
                </div>
                <Link  onClick={onLogoutSuccess }  style={{ cursor: "pointer" }} className='text-white nav-link btn btn-danger underline text-light' to="/">Logout</Link > 
            </ul>

        )

    }
    return (


        <nav className={classNames(scrollPosition > 0 ? 'bg-background' : 'bg-light', ' navbar navbar-expand-lg navbar-light  shadow fixed-top')}>

            <Link className="navbar-brand " to="/">
                <img className='ms-5' src="https://i2.wp.com/renastech.com/wp-content/uploads/2021/03/logonew_da-01-01.png" height='60'></img>
            </Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse me-5" id="navbarNav">
                {links}
            </div>
        </nav>


    )

}

const mapStateToProps = (store) => {
    return {
        isLoggedIn: store.isLoggedIn,
        username: store.userFirstName,
        id: store.id,
        userImage:store.userImage
    }

}
const mapDispatchProps = (dispatch) => {

    return {
        onLogoutSuccess: () => {
            dispatch(logoutSuccess());
        }
    }
}
export default connect(mapStateToProps, mapDispatchProps)(TopBar)
