import * as ACTIONS from "./Constants";
import TokenService from "../api/TokenService"; 
import { login , signup,signout } from "../api/apiCalls";
export const logoutSuccess = () => {
  signout();
  return {
    type: ACTIONS.LOGOUT_SUCCESS,
  };
};
export const loginSuccess = (authData) => {
  return {
    type: ACTIONS.LOGINSUCCESS,
    isLoggedIn: true,
    payload: authData,
  };
};
export const loginHandler = (credentials) => {
  return async function (dispatch) {
    const response = await login(credentials);
    if (response.data.accessToken) {
      TokenService.setUser(response.data);
    }
    const userDto=response.data.userDto;
    const authState = {
      id :userDto.id,
      userFirstName:userDto.userFirstName,
      userGender:userDto.userGender,
      userImage :userDto.userImage,
      userLastName :userDto.userLastName,
      userPhoneNumber : userDto.userPhoneNumber,
      userEmail: credentials.userEmail,
      userPassword: credentials.userPassword,
      
    };
  
    dispatch(loginSuccess(authState));
    return response;
  };
};
export const signupHandler =(user)=>{
  return async function(dispatch){
    await dispatch(signup(user))
  }
}
export const updateSuccess =({userPhoneNumber,userImage,userEmail,userGender})=>{
  return {
    type :ACTIONS.UPDATESUCCESS,
    payload:{
      userEmail,
      userImage,
      userPhoneNumber,
      userGender
    }
  }
}
