import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import TicketControl from "./TicketControl";
import { connect } from "react-redux";

const Payment = (props) => {
    const location = useLocation();
    const flight = location.state.flight;
    const ticketNumber = Number.parseInt(location.state.ticketNumber, 10);

    const travelerList = [];


    const [paymentInfo, setPaymentInfo] = useState({
        flightId: flight.id,
        ticketNumber: ticketNumber,
        travellers: travelerList
    })


    const onClickBuy = (event) => {
        event.preventDefault();
        props.history.push({
            pathname: '/checkoutpage',
            state: paymentInfo
        })
    }

    return (
        <div>
            <body style={{ backgroundColor: "#FAFAFA", fontSize: "13px" }}>
                <div className="event-schedule-area-two bg-light pad100  ">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title">
                                    <div className="title-text">
                                        <h3 style={{ marginTop: "150px" }}>Flight Information</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="tab-content" id="myTabContent">
                                    <div className="tab-pane fade active show" id="home" role="tabpanel">
                                        <div className="table-responsive">
                                            <table className="table">
                                                <thead>
                                                    <tr>
                                                        <th className="text-center" scope="col-2">Airline</th>
                                                        <th className="text-center" scope="col-2">Departure</th>
                                                        <th scope="col-2">Arrival</th>
                                                        <th scope="col-2">From</th>
                                                        <th scope="col-2">To</th>
                                                        <th scope="col-2">Price</th>

                                                    </tr>
                                                </thead>
                                                <tbody >

                                                    <tr className="inner-box">
                                                        <th scope="row">
                                                            <div className="event-date">
                                                                <label name='airline'>{flight.company.companyName} </label>
                                                            </div>
                                                        </th>
                                                        <th scope="row">
                                                            <div className="departureDate">
                                                                <label style={{ fontSize: "15px" }} name='date'>{flight.departureDate + " "}</label>
                                                                <label style={{ margin: "10px" }} name='time'>{flight.departureTime} </label>
                                                            </div>
                                                        </th>
                                                        <th scope="row">
                                                            <div className="arrivalDate">
                                                                <label style={{ fontSize: "15px" }} name='date'>{flight.arrivalDate} </label>
                                                                <label style={{ margin: "10px" }} name='time'> {flight.arrivalTime} </label>
                                                            </div>
                                                        </th>

                                                        <td>
                                                            <div className="">
                                                                <label name='from'>{flight.arrivalAirport.name}</label>
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <div className="to">
                                                                <label name='to'>{flight.departureAirport.name}</label>
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <div className="price">
                                                                <label name='price'>{flight.ticketPrice.amount}</label>
                                                            </div>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{ backgroundColor: "#FAFAFA", marginTop: "100px" }} className="container">
                        <h3>Traveler Info</h3>
                        {
                            [...Array(ticketNumber)].map((elementInArray, index) => (
                                <div key={index} ><TicketControl list={travelerList} onSubmit={onClickBuy}  number={index} /></div>
                            ))}
                    </div>
                <div className="container text-center">
                 <button className="btn btn-primary " onClick={onClickBuy}>Buy Ticket</button>
                </div>                
            </body>
        </div>
    );
};


const mapStateToProps = (store) => {
    return {
        isLoggedIn: store.isLoggedIn,
        id: store.id,
    }

}
export default connect(mapStateToProps)(Payment)